<!--  -->
<template>
  <div class="picker">
    <van-popup
      position="bottom"
      class="popup"
      :value="show"
      @input="(val) => this.$emit('input', val)"
      @click-overlay="closeShow"
      @touchmove.prevent
    >
      <van-area
        :title="title"
        :area-list="areaList"
        :value="value"
        :columns-num="columnsNum"
        :columns-placeholder="['', '', '']"
        @confirm="selectValue"
        @cancel="closeShow"
      />
    </van-popup>
  </div>
</template>

<script>
import { Area, Popup } from "vant";
import AreaList from "./area";
import "vant/lib/index.css";

export default {
  name: "Van2Address",
  components: {
    "van-area": Area,
    "van-popup": Popup,
  },
  props: {
    // 
    show: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
    // 
    title: {
      default: "",
      type: String,
    },
    // 
    columnsNum: {
      default: () => 3,
      type: Number,
    },
    value: {
      default: () => "",
      type: [String, Number],
    },
    defaultCode: {
      default: () => "",
      type: String,
    },
  },
  data() {
    return {
      areaList: AreaList,
    };
  },
  methods: {
    closeShow() {
      this.$emit("closeShow");
    },
    selectValue(arr) {
      this.closeShow();
      this.$emit("selectValue", arr);
    },
  },
};
</script>

<style lang="scss" scoped>
.picker {
}
</style>
