<template>
  <!--  -->
  <!--
    
    <el-button type="primary" @click="isShowDialog = true" @sureClick="sureClick"></el-button>
    <ElDialog :show.sync="isShowDialog" title=""></ElDialog>
   -->
  <el-dialog
    :title="title"
    :visible.sync="visible"
    top="5vh"
    :width="width"
    :show="show"
    @close="$emit('update:show', false)"
  >
    <!--  -->
    <slot></slot>
    <!--  -->
    <div slot="footer" class="dialog-footer">
      <el-button @click="closeDialog">   </el-button>
      <el-button type="primary" @click="sureClick">   </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { Dialog, Button } from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
export default {
  name: "El2Dialog",
  components: {
    "el-dialog": Dialog,
    "el-button": Button,
  },
  props: {
    title: {
      type: String,
      default: () => {
        return "";
      },
    },
    // 
    show: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
    // 
    width: {
      type: String,
      default: "50%",
    },
  },
  data() {
    return {
      visible: this.show,
    };
  },
  computed: {},
  watch: {
    show() {
      this.visible = this.show;
    },
  },
  created() {},
  mounted() {},
  methods: {
    closeDialog() {
      this.$emit("update:show", false);
    },
    sureClick() {
      this.$emit("sureClick");
      this.$emit("update:show", false);
    },
  },
};
</script>
<style scoped></style>
